.todo{background-image:linear-gradient(0deg, #aaffa9 10%, #11ffbd 70%);padding:10%}.todo .todo_wrap{background-color:#fff;margin:0 auto;padding:4%;width:50%}.todo .todo_logo{text-align:center}.todo .js_todo{display:flex;flex-direction:column}.todo .logo_image{margin:0 auto;width:10%}.todo .logo_text{font-size:20px;margin-top:20px}.todo .todo_list{display:flex;justify-content:space-between}.todo .todo_list:not(:first-child){margin-top:15px}.todo .todo_contents{border:1px solid #000;font-weight:bold;font-size:20px;text-align:center;padding:5px;width:90%}.todo .delete{background-color:red;border-radius:10px;border:1px solid red;color:#fff;font-size:30px;width:7%}.todo .delete:hover{background-color:#fff;color:red}.todo .todo_add{display:flex;flex-direction:column;justify-content:center}.todo .add_text{font-size:16px;margin:15px auto;padding:5px;width:80%}.todo .add{background-color:blue;border-radius:10px;border:1px solid blue;color:#fff;font-size:30px;margin:auto;width:7%}.todo .add:hover{background-color:#fff;color:blue}button{cursor:pointer}button:hover{transition:background .3s}
