body,div,dl,dt,dd,ul,ol,li,h1,h2,h3,h4,h5,h6,pre,form,fieldset,input,textarea,p,blockquote,th,td{margin:0;padding:0}html{overflow-y:scroll}body{max-width:1440px;margin:auto}h1,h2,h3,h4,h5,h6{font-size:100%;font-weight:normal}ol,ul{list-style:none}fieldset,img{border:0}img{max-width:100%;vertical-align:bottom}table{border-collapse:collapse;border-spacing:0}caption,th{text-align:left}a:focus{outline:none}address,caption,cite,code,dfn,em,th,var{font-style:normal;font-weight:normal}a,input{color:#000;text-decoration:none;-webkit-transition:0.3s ease-in-out;-moz-transition:0.3s ease-in-out;-o-transition:0.3s ease-in-out;transition:0.3s ease-in-out}a:hover,input:hover{cursor:pointer}button{outline:none;border:none;background:inherit}
